

.alert-title-none{
  display: none;
}

.meteo-card-container {
  width: 100%;
  display: flex;
  justify-content: space-evenly;
  flex-wrap: wrap;
}

.meteo-card {
  margin-top: 20px;
  width: 12%;
}

.meteo-card img {
  width: 66%;
}

