html, body {
  height: 100%;
  width: 100%;
}

.main-container {
  height: 100%;
  width: 100%;
}



@media screen and (max-width: 760px) {

  .meteo-card-container {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
  }

  .meteo-card {
    margin-top: 20px;
    width: 66%;
    align-self: center;
  }

}